import axios from 'axios'
import base from './base'
// const qs = require('querystring')
const api = {
    login (params) {
        return axios.get(base.login, {
            params
        })
    },
    getCourse (params) {
        return axios.get(base.getCourse, {
            params
        })
    },
    setCourse (params) {
        return axios.get(base.setCourse, {
            params
        })
    },
    getActive (params) {
        return axios.get(base.getActive, {
            params
        })
    },
    signIn (params) {
        return axios.get(base.signIn, {
            params
        })
    }
}
export default api;