<template>
  <div class="container">
    <el-table
      :data="tableData"
      style="max-width: 80%"
      class="table"
      :key="tableData.id"
    >
      <el-table-column prop="name" label="名称" />
      <el-table-column prop="time" label="时间" />
      <el-table-column label="操作" min-width="200px">
        <template #default="scope">
          <div class="action-container">
            <el-select
              v-model="scope.row.selectedOption"
              placeholder="选择位置"
              size="default"
              style="margin-right: 10px"
              :disabled="scope.row.type !== '4'"
            >
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
            <el-button @click="signIn(scope.row)" size="default"
              >签到</el-button
            >
          </div>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import { ElMessage } from 'element-plus'
export default {
  data () {
    return {
      tableData: [],
      options: [
        {
          value: 'one',
          label: '一号楼'
        },
        {
          value: 'two',
          label: '二号楼'
        },
        {
          value: 'three',
          label: '三号楼'
        },
        {
          value: 'four',
          label: '汇文楼'
        }
      ]
    }
  },
  methods: {
    getActived () {
      this.$api.getActive().then(res => {
        const arr = res.data.data.activeList
        console.log(res)
        if (arr.length === 0) {
          ElMessage({
            message: '该课程没有发布过签到任务',
            type: 'error'
          })
        } else {
          arr.forEach(ele => {
            if (ele.type === 2) {
              this.tableData.push({
                name: ele.nameOne,
                time: ele.nameFour,
                type: ele.otherId,
                id: ele.id
              })
            }
          })
          if (this.tableData.length === 0) {
            ElMessage({
              message: '该课程没有发布过签到任务',
              type: 'error'
            })
          }
        }
      })
    },
    signIn (row) {
      console.log(row.type)
      if (row.type === '4') {
        this.$api.signIn({
          id: row.id,
          position: row.selectedOption,
          type: row.type
        }).then(res => {
          console.log(res)
        })
      } else {
        this.$api.signIn({
          id: row.id,
          type: row.type
        }).then(res => {
          console.log(res)
        })
      }
    }
  },
  mounted () {
    this.getActived()
  }
}
</script>

<style>
.table {
  padding: 30px;
  padding-top: 40px;
  padding-bottom: 40px;
}
.action-container {
  display: flex;
  align-items: center;
}
</style>
