<template>
  <div class="card-container">
    <el-card
      v-for="element in course"
      :key="element.id"
      class="card"
      @click="getEvent(element)"
    >
      <template #header>
        <div class="card-header">
          <span
            >{{ element.content.course.data[0].name }}({{
              element.content.course.data[0].teacherfactor
            }})</span
          >
        </div>
      </template>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'courseView',
  data () {
    return {
      course: [],
      fit: 'cover'
    }
  },
  methods: {
    getCourse () {
      this.$api.getCourse().then(res => {
        if (res.status === 200) {
          this.course = res.data.slice(1)
          console.log(this.course)
        }
      })
    },
    getEvent (element) {
      this.$api.setCourse({
        courseId: element.content.course.data[0].id,
        classId: element.content.id
      }).then(res => {
        console.log(res)
        this.$router.push({ path: '/event' })
      })
    }
  },
  mounted () {
    this.getCourse()
  }
}
</script>

<style>
.card-container {
  display: flex;
  flex-wrap: wrap;
  gap: 16px; /* 卡片之间的间距 */
  justify-content: space-around; /* 卡片在容器中均匀分布 */
  padding: 40px;
}
.card {
  margin-bottom: 30px;
  flex: 1 1 calc(33.333% - 32px);
  max-width: 400px;
  height: 160px;
  box-sizing: border-box;
}
.card:hover {
  cursor: pointer;
}
</style>
