<template>
  <div class="container">
    <el-form
      ref="ruleFormRef"
      :model="ruleForm"
      status-icon
      :rules="rules"
      label-width="auto"
      class="ruleForm"
      size="large"
    >
      <el-form-item label="用户名" prop="username">
        <el-input
          v-model="ruleForm.username"
          type="text"
          autocomplete="off"
          style="width: 200px"
        />
      </el-form-item>
      <el-form-item label="密码" prop="password">
        <el-input
          v-model="ruleForm.password"
          type="password"
          autocomplete="off"
          style="width: 200px"
        />
      </el-form-item>
      <el-button type="primary" @click="submitForm()"> 确认 </el-button>
    </el-form>
  </div>
</template>

<script>
import { ElMessage } from 'element-plus'

export default {
  name: 'loginView',
  data () {
    return {
      ruleForm: {
        username: '',
        password: ''
      },
      rules: {
        username: [
          { validator: this.validateUsername, trigger: 'blur' }
        ],
        password: [
          { validator: this.validatePassword, trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    validateUsername (rule, value, callback) {
      if (value === '') {
        callback(new Error('请输入用户名'))
      } else {
        if (this.ruleForm.checkPass !== '') {
          this.$refs.ruleFormRef.validateField('checkPass')
        }
        callback()
      }
    },
    validatePassword (rule, value, callback) {
      if (value === '') {
        callback(new Error('请输入密码'))
      } else {
        callback()
      }
    },
    submitForm () {
      this.$refs.ruleFormRef.validate((valid) => {
        if (valid) {
          this.$api.login(this.ruleForm).then(res => {
            console.log()
            if (res.status === 200) {
              this.$router.push({ path: '/course' })
            } else {
              ElMessage({
                message: res.msg,
                type: 'error'
              })
            }
          })
        }
      })
    },
    resetForm () {
      this.$refs.ruleFormRef.resetFields()
    }
  }
}
</script>

<style>
.container {
  height: inherit;
  display: flex;
  justify-content: center;
  flex-direction: column; /* 垂直排列子元素 */
  align-items: center;
}
.ruleForm {
  padding: 100px;
  border-radius: 10px;
  background-color: #94a3b8;
  display: flex;
  flex-direction: column;
  align-items: center; /* 水平居中对齐子元素 */
}
.el-form-item {
  margin-bottom: 30px;
}
.el-input {
  border-radius: 20px;
}
</style>
